import React, { Component } from 'react';
import ReactGA from 'react-ga';
import $ from 'jquery';
import './App.css';
import { IntlProvider } from 'react-intl';
import { addLocaleData } from 'react-intl';
import en from 'react-intl/locale-data/en';
import fa from 'react-intl/locale-data/fa';
import Header from './Components/Header';
import Footer from './Components/Footer';
import About from './Components/About';
import Resume from './Components/Resume';
import Contact from './Components/Contact';
import Testimonials from './Components/Testimonials';
import Portfolio from './Components/Portfolio';

addLocaleData([...en, ...fa]);
const language = navigator.language.split(/[-_]/)[0];

class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      resumeData: {},
        lang:'en',
    };

    ReactGA.initialize('UA-110570651-1');
    ReactGA.pageview(window.location.pathname);
  }

  getResumeData(){
    $.ajax({
      url:`/resumeData-${this.state.lang}.json`,
      dataType:'json',
      cache: false,
      success: function(data){
        this.setState({resumeData: data});
      }.bind(this),
      error: function(xhr, status, err){
        console.log(err);
    alert(err);
}
});
}

componentDidMount(){
    this.getResumeData();
}

  render() {
    return (
        <IntlProvider locale={this.state.lang} timeZone="Asia/Tehran">
        <div className="App">
        <Header data={this.state.resumeData.main} lang={(this.state.lang === 'en' ? 'fa' : 'en')} langCallback={this.onChangeLang}/>
        <About data={this.state.resumeData.main}/>
        <Resume data={this.state.resumeData.resume}/>
        <Portfolio data={this.state.resumeData.portfolio}/>
        {/*<Testimonials data={this.state.resumeData.testimonials}/>*/}
        {/*<Contact data={this.state.resumeData.main}/>*/}
        <Footer data={this.state.resumeData.main}/>
      </div>
        </IntlProvider>
    );
  }

  onChangeLang = () => {
      const l = this.state.lang;
      this.setState({lang: l === 'en' ? 'fa' : 'en'}, ()=> this.getResumeData());
  }
}

export default App;



